.skill {
    width: 250px;
    background-color: rgba(255, 255, 0, 0.7);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

}

.icon {
    height: 64px;
    width: 64px;

    background-size: cover;
    background-position: center;
}

.HTML {
    background-image: url("../../../common/icons/icons8-html.svg");
}

.Css {
    background-image: url("../../../common/icons/icons8-Css.svg");
}

.Javascript {
    background-image: url("../../../common/icons/icons8-javascript.svg");
}

.Typescript {
    background-image: url("../../../common/icons/icons8-typescript.svg");
}

.React {
    background-image: url("../../../common/icons/icons8-react.svg");
}

.Redux {
    background-image: url("../../../common/icons/icons8-redux.svg");
}



.description {
    padding: 10px;
}